.form-inline {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.form-inline .form-check {
  width: 100%;
}

@media (min-width: 576px) {
  .form-inline label {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0;
  }

  .form-inline .form-group {
    display: flex;
    flex: 0 0 auto;
    flex-flow: row wrap;
    align-items: center;
    margin-bottom: 0;
  }

  .form-inline .form-control {
    display: inline-block;
    width: auto;
    vertical-align: middle;
  }

  .form-inline .form-control-plaintext {
    display: inline-block;
  }

  .form-inline .input-group {
    width: auto;
  }

  .form-inline .form-control-label {
    margin-bottom: 0;
    vertical-align: middle;
  }

  .form-inline .form-check {
    display: flex;
    align-items: center;
    justify-content: center;
    width: auto;
    margin-top: 0;
    margin-bottom: 0;
  }

  .form-inline .form-check-label {
    padding-left: 0;
  }

  .form-inline .form-check-input {
    position: relative;
    margin-top: 0;
    margin-right: 0.25rem;
    margin-left: 0;
  }

  .form-inline .custom-control {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 0;
  }

  .form-inline .custom-control-indicator {
    position: static;
    display: inline-block;
    margin-right: 0.25rem;
    vertical-align: text-bottom;
  }

  .form-inline .has-feedback .form-control-feedback {
    top: 0;
  }
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  overflow: visible;
  clip: auto;
  white-space: normal;
  clip-path: none;
}

.map-area {
  display: flex;
  flex-wrap: wrap;
  margin-top: 27px;
  margin-right: 0;
  margin-left: 0;
  flex-direction: row;
}

@media (min-width: 768px) {
  .map-area {
    flex-direction: row-reverse;
  }
}

.map-area .loc-list {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  min-height: 1px;
  flex: 0 0 100%;
  max-width: 100%;
}

@media (min-width: 768px) {
  .map-area .loc-list {
    flex: 0 0 25%;
    max-width: 25%;
  }
}

@media (min-width: 992px) {
  .map-area .loc-list {
    flex: 0 0 25%;
    max-width: 25%;
  }
}

.map-area .map-container {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  min-height: 1px;
  flex: 0 0 100%;
  max-width: 100%;
}

@media (min-width: 768px) {
  .map-area .map-container {
    flex: 0 0 75%;
    max-width: 75%;
  }
}

@media (min-width: 992px) {
  .map-area .map-container {
    flex: 0 0 75%;
    max-width: 75%;
  }
}

.locator input[type="submit"],
.locator input[type="reset"] {
  float: none;
}

/* ===
 Map
=== */

.map-container a {
  color: #e76737;
  text-decoration: none;
}

.map-container a:hover,
.map-container a:active {
  text-decoration: underline;
}

.map-container .custom-marker {
  width: 32px;
  height: 37px;
  color: #fff;
  padding: 3px;
  cursor: pointer;
}

.marker-content {
  margin: 2px;
}

.map {
  float: right;
  width: 100%;
  height: 530px;
}

.gm-style .cluster img {
  margin: 0;
  border: 0;
  padding: 0;
}

.loc-name {
  color: #AD2118;
  font-weight: bold;
}

/* ===
 Loading
=== */

.typography img.loading {
  border: none;
}

/* ===
 Modal window
=== */

#overlay {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 10000;
  background: url(../images/overlay-bg.png) repeat;
}

#modal-window {
  position: absolute;
  left: 50%;
  margin-left: -460px;
  /* width divided by 2 */
  margin-top: 60px;
  width: 920px;
  height: 590px;
  z-index: 10010;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px #656565;
}

#modal-content {
  padding: 0 22px;
  /* there's already a margin on the top of the map-container div */
}

#close-icon {
  position: absolute;
  top: -6px;
  right: -6px;
  width: 18px;
  height: 18px;
  cursor: pointer;
  background: #2c2c2c url(../images/close-icon.png) 3px 3px no-repeat;
  border: 1px solid #000;
  border-radius: 3px;
  box-shadow: 0 0 3px #656565;
}

/* #Tablet (Portrait)
================================================== */

/* Note: Design for a width of 768px */

@media only screen and (min-width: 641px) and (max-width: 959px) {
  .loc-list .list-label {
    margin-left: 0px;
  }

  .loc-list .list-content {
    padding: 10px 0px;
  }

  .loc-list .list-details {
    width: 140px;
  }
}

.loc-list {
  height: 530px;
}

.loc-list .list-label {
  float: left;
  margin: 10px 0 0 6px;
  padding: 2px 3px;
  width: 27px;
  text-align: center;
  background: #451400;
  color: #fff;
  font-weight: bold;
}

.loc-list .list-details {
  float: left;
  margin-left: 6px;
  width: 165px;
}

.loc-list .list-content {
  padding: 10px;
}

.loc-list .loc-dist {
  font-weight: bold;
  font-style: italic;
  color: #8e8e8e;
}

.loc-list .list-location {
  display: block;
  box-sizing: border-box;
  clear: left;
  float: left;
  margin: 6px 0;
  cursor: pointer;
  width: 99%;
  border: 1px solid transparent;
}

.loc-list .list-location.first {
  margin-top: 0;
}

.loc-list .list-location.last {
  margin-bottom: 0;
}

.loc-list .list-location.even {
  background-color: #eeeeee;
}

.loc-list .list-location.focus {
  border: 1px solid rgba(82, 168, 236, 0.9);
  box-shadow: 0 0 8px rgba(82, 168, 236, 0.7);
  transition: border 0.2s linear 0s, box-shadow 0.2s linear 0s;
  z-index: 1;
}

.ReactVirtualized__List:focus {
  border: none;
  outline: none;
}

.ReactVirtualized__Grid__innerScrollContainer {
  padding-bottom: 10px;
}




